const mouseClickEvents = ['mousedown', 'click', 'mouseup'];
function simulateMouseClick(element){
  mouseClickEvents.forEach(mouseEventType =>
    element.dispatchEvent(
      new MouseEvent(mouseEventType, {
          view: window,
          bubbles: true,
          cancelable: true,
          buttons: 1
      })
    )
  );
}

  function addEditValues(data) {
    Object.entries(data).forEach((item) => {
      // console.log(item);
      if(item[0] == 'imagen') {
        let input = document.querySelector(`[name=${item[0]}]`);
        let img = document.querySelector('.image-preview-form');
        if(input) {
          input.value = item[1];
        }
        if(img) {
          img.src = item[1];
        }
      } else {
        let input = document.querySelector(`[name=${item[0]}]`);
        if(input) {
          input.value = item[1];
        }
      }
      
    });
  }

