import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Modal from "@material-ui/core/Modal";
import Button from './Button';

function rand() {
  return Math.round(Math.random() * 20) - 10;
}

function getModalStyle() {
  const top = 50 + rand();
  const left = 50 + rand();

  return {
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    margin: 'auto'
  };
}

const useStyles = makeStyles((theme) => ({
  paper: {
    position: "absolute",
    width: "80%",
    minWidth: 400,
    maxWidth: 900,
    height: "auto",
    maxHeight: "90%",
    backgroundColor: theme.palette.background.paper,
    // border: "2px solid #000",
    boxShadow: theme.shadows[5],
    padding: theme.spacing(2, 4, 3),
    overflow: "auto",
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
    flexDirection: "column"
  },
}));

export default function SimpleModal(props) {
  const classes = useStyles();
  // getModalStyle is not a pure function, we roll the style only on the first render
  const [modalStyle] = React.useState(getModalStyle);
  const [open, setOpen] = React.useState(false);

  const handleOpen = async () => {
    await setOpen(true);
    let elemModal = document.querySelector('.makeStyles-paper-custom');
    let elemBody = document.querySelector('.all-content').offsetHeight;
    elemModal.style.height = elemBody + 'px';
  };

  const handleClose = () => {
    setOpen(false);
  };
  
  function addSelectedRow(e) {
    let thisTarget = e.currentTarget;
    let prevSelected = document.querySelector('.cell-selected');
    if(prevSelected) {
     if(prevSelected != thisTarget)
     prevSelected.classList.remove('cell-selected');
    }
    thisTarget.classList.toggle('cell-selected');
  }

  function updateItemNumber() {
    let itemNumber = document.querySelectorAll('.item-number');
    itemNumber.forEach((item, i) => {
      item.textContent = i + 1;
    });
  }

  const body = (
    <div style={modalStyle} className={classes.paper + ' makeStyles-paper-custom'}>
      <div className="close-modal" onClick={handleClose}><i class="fas fa-times"></i></div>
      <div className="all-content">
        <div className="title-area">{props.Struct.referer}</div>
        <form>
          {props.Struct.data.map((item) => {
            return item.type != "table" ? (
              <div className="row">
                <div className="col-25">
                  <label>{item.referer}</label>
                </div>
                <div className="col-75">
                  {item.type == "select" ? (
                    <select name={item.name}>
                      <option>Test</option>
                    </select>
                  ) : item.type == "textarea" ? (
                    <textarea name={item.name}></textarea>
                  ) : item.type == "number" ? (
                    <input name={item.name} type={item.type} min="0"></input>
                  ) : item.type == "imagen" ? (
                    <div className="form-img">
                      <input name={item.name} type="text"></input>
                      <img src="" className="imagen-form image-preview-form"></img>
                    </div>
                  ) : (
                    <input name={item.name} type={item.type}></input>
                  )}
                </div>
              </div>
            ) : (
              <div className="row">
                <div className="col-100">
                  <div className="table-container">
                    <div className="actions-table">
                      <Button title="Nueva Fila" icon="fas fa-plus" class="add-color" onClick={() => {
                        let tbody = document.querySelector('.tbody-table');
                        let elementsTh = document.querySelector('.thead-table').querySelectorAll('th');
                        let tr = document.createElement('tr');
                        elementsTh.forEach((item) => {
                          let td = document.createElement('td');
                          console.log(item.textContent.trim());
                          if(item.textContent.trim() == 'Código') {
                            td.innerHTML = '<input type="text" className="codigo"></input>'
                          } else if(item.textContent.trim() == 'Item') {
                            td.innerHTML = '<span class="item-number"></span>'
                          }
                          tr.appendChild(td);
                        })
                        tr.addEventListener('click', addSelectedRow);
                        tbody.appendChild(tr);
                        updateItemNumber();
                      }}></Button>
                      <Button title="Borrar Fila" icon="fas fa-trash-alt" class="delete-color" onClick={() => {
                        let selectedRow = document.querySelector('.cell-selected');
                        if(selectedRow) {
                          selectedRow.remove();
                          updateItemNumber();
                        } else {
                          alert('Debe de seleccionar alguna fila para poder eliminar');
                        }
                      }}></Button>
                    </div>
                    <table className="table-custom-content styled-table">
                      <thead className="thead-table">
                        <tr>
                          {item.value.map((col) => {
                            return <th>{col}</th>;
                          })}
                        </tr>
                      </thead>
      
                       <tbody className="tbody-table">
                        {props.dataTest.map((item) => {
                            return (
                              <tr onClick={addSelectedRow}>
                                {Object.entries(item).map((value) => {
                                return <td>
                                    {value[0] == "codigo" ? (
                                      <input type="text" className="codigo" value={value[1]}></input>
                                    ) : value[0] == "imagen" ? <img src={value[1]} className="img-table"></img>: (
                                      value[0] == "item" ? (
                                        <span className="item-number">{value[1]}</span>
                                      ) : value[1]
                                    )}
                                  </td>;
                                })}
                              </tr>
                            );
                          })}
                       </tbody>
                 
                    </table>
                  </div>
                </div>
              </div>
            );
          })}
        </form>
        <div class="form-btn">
          <button class="btn-save" type="submit">
            Registrar
          </button>
        </div>
        {/* <div class="view-report">
          <a class="btn-view-report" href="/report/inventario/">
            Visualizar Reporte
          </a>
        </div>
        <div class="back-page">
          <a href="/" class="btn-back-page">
            Volver al Inicio
          </a>
        </div> */}
      </div>
    </div>
  );

  return (
    <div className="modal-content">
      <Button class="open-modal" title="Nuevo" color="primary" icon="fas fa-plus" onClick={handleOpen}>
            </Button>
      <Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="simple-modal-title"
        aria-describedby="simple-modal-description"
      >
        {body}
      </Modal>
    </div>
  );
}
