import React from 'react';

class UserInfo extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }

  render() {
    return <div className="extend-info-user">
    <div className="foto-user">
      <img src={this.props.image || "/profile.png"}></img>
    </div>
    <div className="name-user">
      { this.props.name }
    </div>
    <div className="email-user">
      { this.props.email }
    </div>
    <div className="exit-sesion">
      Cerrar sesión
    </div>
  </div>;
  }
}

export default UserInfo;