import React from 'react';

let propsSections = [];
let searchHistory = '';
const mouseClickEvents = ['mousedown', 'click', 'mouseup'];

function simulateMouseClick(element){
  mouseClickEvents.forEach(mouseEventType =>
    element.dispatchEvent(
      new MouseEvent(mouseEventType, {
          view: window,
          bubbles: true,
          cancelable: true,
          buttons: 1
      })
    )
  );
}

class Navbar extends React.Component {
  constructor(props) {
    super(props);
    this.state = {

    }
  }

  componentDidMount() {
    let sections = document.querySelectorAll('.option-dashboard');
    let dropSections = document.querySelectorAll('.dropdown-section');
    sections.forEach((item, i, vec) => {
        dropSections[i].querySelectorAll('.item-section').forEach((drop) => {
          propsSections.push({
            name: item.textContent,
            children: drop.querySelector('.name-item').textContent,
            target: drop.href
          })
        })
     
    });
    propsSections.push({
      name: 'Inicio',
      children: '',
      target: '/'
    })
    window.document.addEventListener("click", (e) => {
        if (
          !e.target.closest(".extend-info-user") &&
          !e.target.closest(".user-info")
        ) {
          let extendInfo = document.querySelector(".extend-info-user");
          extendInfo.classList.remove('d-flex-show');
        }
    });
  }

  render() {
    return <div className="navbar">
      <div className="navbar-content">
        <div className="menu-bars" onClick={this.toggleSidebar.bind(this)}>
          <div className="btn-menu"><i class="fas fa-bars"></i></div>
        </div>
        <div className="title-logo">Grupo San Patricio</div>
        
        <div className="user-login">
          <div className="user-info" onClick={this.toggleUserInfo.bind(this)}><img src="/profile.png"></img></div>
          
        </div>
        <div className="search-section">
          <input type="text" className="input-search" placeholder="Buscar..." onKeyUp={this.searchAll.bind(this)} onFocus={this.searchFocusAll.bind(this)} onBlur={this.searchBlurAll.bind(this)}></input>
          <div className="search-button">
            <i class="fas fa-search"></i>
          </div>
        </div>
      </div>
    </div>;
  }
  
  toggleSidebar(e) {
    let sidebar = document.querySelector('.sidebar');
    let blockContainer = document.querySelector('.block__container__sidebar');
    if(sidebar.style.display == "none" && sidebar.style.display) {
      sidebar.style.display = "block";
      let container = document.querySelector('.container');
      container.classList.remove('extend-block-left');
      let navbar = document.querySelector('.navbar');
      navbar.classList.remove('extend-block-left');
      if(window.innerWidth < 568) {
        blockContainer.classList.remove('d-none');
      }
    } else {
      sidebar.style.display = "none";
      let container = document.querySelector('.container');
      container.classList.add('extend-block-left');
      let navbar = document.querySelector('.navbar');
      navbar.classList.add('extend-block-left');
      if(window.innerWidth < 568) {
        blockContainer.classList.add('d-none');
      }
    }
  }
  
  toggleUserInfo(e) {
    let infoExtend = document.querySelector('.extend-info-user');
    infoExtend.classList.toggle('d-flex-show');
  }

  searchAll(e) {
    let thisValue = e.currentTarget.value;
    let searchResults = document.querySelector('.search-results');
    console.log(e.keyCode);
    if(e.keyCode == 38) {
      //Arriba
      // e.preventDefault();
      let selectedItem = document.querySelector('.selected-item-search');
      let allItemsSearch = document.querySelectorAll('.search-item-result');
      let newIndex = 0;
      allItemsSearch.forEach((v, i, vec) => {
        if(i > 0) {
          if(v == selectedItem) {
            newIndex = i - 1;
          }
        }
      });
      // let firstItemSearch = allItemsSearch[0];
      if(!selectedItem) {
        // firstItemSearch.classList.add('selected-item-search');
      } else {
        selectedItem.classList.remove('selected-item-search');
        allItemsSearch[newIndex].classList.add('selected-item-search');
        e.currentTarget.value =  allItemsSearch[newIndex].textContent;
        searchResults.scroll({
          top: allItemsSearch[newIndex].offsetTop,
          behavior: "smooth",
        });
      }
    } else if(e.keyCode == 40) {
      //Abajo
      let selectedItem = document.querySelector('.selected-item-search');
      let allItemsSearch = document.querySelectorAll('.search-item-result');
      let newIndex = 0;
      allItemsSearch.forEach((v, i, vec) => {
        if(i < vec.length - 1) {
          if(v == selectedItem) {
            newIndex = i + 1;
          }
        }
      });
      let firstItemSearch = allItemsSearch[0];
      if(!selectedItem) {
        firstItemSearch.classList.add('selected-item-search');
        e.currentTarget.value = firstItemSearch.textContent;
        searchResults.scroll({
          top: firstItemSearch.offsetTop,
          behavior: "smooth",
        });
      } else {
        selectedItem.classList.remove('selected-item-search');
        allItemsSearch[newIndex].classList.add('selected-item-search');
        e.currentTarget.value =  allItemsSearch[newIndex].textContent;
        searchResults.scroll({
          top: allItemsSearch[newIndex].offsetTop,
          behavior: "smooth",
        });
      }
    } else if(e.keyCode == 27) {
      let selectedItem = document.querySelector('.selected-item-search');
      selectedItem.classList.remove('selected-item-search');
      e.currentTarget.value = searchHistory;
    } else if(e.keyCode == 13) {
      let selectedItem = document.querySelector('.selected-item-search');
      simulateMouseClick(selectedItem);
    }
    
    else {
      if(thisValue != '') {
        searchHistory = thisValue;
        let html = '';
        let filterSearch = propsSections.filter((v) => v.name.toLowerCase().includes(thisValue.toLowerCase()) || v.children.toLowerCase().includes(thisValue.toLowerCase()));
        if(thisValue.split(' ')) {
          if(thisValue.split(' ').length > 1) {
            let filterExtend = propsSections.filter((v) => v.name.toLowerCase().includes(thisValue.split(' ')[0].toLowerCase()) && v.children.toLowerCase().includes(thisValue.split(' ')[1].toLowerCase()));
            filterSearch = filterSearch.concat(filterExtend);
          }
        }
        if(thisValue.split('/')) {
          if(thisValue.split('/').length > 1) {
            let filterExtend = propsSections.filter((v) => v.name.toLowerCase().includes(thisValue.split('/')[0].trim().toLowerCase()) && v.children.toLowerCase().includes(thisValue.split('/')[1].trim().toLowerCase()));
            filterSearch = filterSearch.concat(filterExtend);
          }
        }
        // console.log(filterSearch);
        
        filterSearch.forEach((item) => {
          html += `<a href=${item.target} class="search-item-result"><div class="item-result">${item.name}</div> <div class="separator-result">/</div> <div class="subitem-result">${item.children}</div></a>`
        })
        searchResults.innerHTML = html;
        if(html != '') {
          searchResults.classList.remove('d-none');
        } else {
          searchResults.classList.add('d-none');
        }
      } else {
        searchResults.classList.add('d-none');
      }
    }
  }
  searchFocusAll() {
    let searchResults = document.querySelector('.search-results');
    let inputSearch = document.querySelector('.input-search');
    if(inputSearch.value != '') {
      searchResults.classList.remove('d-none');
    }
  }
  searchBlurAll() {
    let searchResults = document.querySelector('.search-results');
    setTimeout(function() {
      searchResults.classList.add('d-none');
    },100);
  }
}

export default Navbar;