import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';

const useStyles = makeStyles((theme) => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
    },
  },
}));

export default function ContainedButtons(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      {
        props.disabled ? <Button disabled variant="contained" className={props.class ? props.class : ''} color={props.color} onClick={props.onClick}>
        <i className={props.icon}></i> {props.title == '' ? '' : <div>&nbsp;</div>} {props.title}
      </Button> : 
      <Button variant="contained" className={props.class ? props.class : ''} color={props.color} onClick={props.onClick}>
      <i className={props.icon}></i> {props.title == '' ? '' : <div>&nbsp;</div>} {props.title}
    </Button> 
      }
      
    </div>
  );
}
